import React, { Component} from 'react';
import nextId from "react-id-generator";
import strings from "../../Localisation";
import {sendAmplitudeData} from "../../../utils/Amplitude";
let classNames = require('classnames');

export default class Card extends Component {

    constructor(props) {
        super(props);
        this.cardId = nextId();
    }

    componentDidMount() {
        let link = this;
        let card = document.getElementById(this.cardId);

        card.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("Media: opening " + link.props.path);
            window.open(link.props.path, '_blank');
        })
    }

    render() {
        let className = classNames('media_card', {
            'media_card__marginal': this.props.isMargin === true,
            'media_card__active': this.props.src
        });

        return (
                <div className={className} id={this.cardId}>
                    <div className={"media_card__header"}>
                        <img className={"media_card__image"} src={this.props.src} />
                        <div className={"media_card__title"}>{this.props.title}</div>
                    </div>

                    <div className={"media_card__description"}>{this.props.description}</div>
                </div>
        )
    }
}